.main{
    height: 40vh; //fallback option
    height: calc(var(--vh, 1vh) * 40);
    background-color: $white;
    @include media('<=phone'){
        height: 50vh
    }
    .row{
        flex-direction: column;
        justify-content: center;
        h2{

            margin-top: 15vh;
            font-size: 1.8vmax;
            line-height: 3vmax;
            font-weight: 700;
            z-index: 10;
            color: $white;
            mix-blend-mode: difference;
            @include media('<=tablet'){
                margin-top: 5vmax;
                font-size: 2vmax;
                line-height: 3vmax;
            }
            @include media('<=phone'){
                margin-top: 10vmax;
                font-size: 2.2vmax;
                line-height: 3.0vmax;
            }
            .line{
                margin-bottom: 2vmax;
                height: 3vmax;
                position: relative;
                overflow: hidden;
                @include media('<=tablet'){
                    height: 5vmax;
                }
                @include media('<=phone'){
                    height: 6vmax;
                }}
                span{
                    position: absolute;
                    a{
                        color: rgba(190, 190, 190, 0.692);
                    }
            }
        }
    }
}