
// intro overlay
.intro-overlay {
    .top {
      height: 40vh;
      height: calc(var(--vh, 1vh) * 40);
      position: absolute;
      width: 100%;
      z-index: 8;
      .overlay-top {
        position: absolute;
        height: 100%;
        width: 100%;
        background: $black;
        bottom: 0;
        left: 0; //FOR ANIMATION RIGHT TO LEFT COMMENT
        right: 0;
        @include media("<=tablet") {
          width: 100vw;
        }
      }
    }
    .bottom{
      height: 50vh;
      height: calc(var(--vh, 1vh) * 50);
      position: absolute;
      bottom: 10vh;
      width: 100%;
      z-index: 8;
      .overlay-bottom{
        position: absolute;
        height: 100%;
        width: 33.333vw;
        background: $black;
        bottom: 0;
        right: 66.666%;
        @include media("<=tablet") {
          right: 0;
          width: 100vw;
        }
        &:nth-child(2) {
          right: 33.333%;
          @include media("<=tablet") {
            width: 100vw;
            top: 100%;
            right: 0;
          }
        }
        &:nth-child(3) {
          right: 0;
          @include media("<=tablet") {
            width: 100vw;
            top: 200%;
          }
        }
      }
    }
    .footer-overlay{
        height: 10vh;
        height: calc(var(--vh, 1vh) * 10);
        position: absolute;
        bottom: 0;
        width: 100%;
        z-index: 8;
        .overlay-footer{
            position: absolute;
            height: 100%;
            width: 100%;
            background: $black;
            bottom: 0;
            left: 0; //FOR ANIMATION RIGHT TO LEFT COMMENT
            right: 0;
            @include media("<=tablet") {
                width: 100vw;
              }
        }
  }
}