.sectionFirst {
    background: $white;
    position: relative;
    padding: 0 24px 60px;
    display: flex;
    flex-direction: column;
    align-items: left;
    .pic {
      height: 40vh;
      width: 40vh;
      background: $white;
      box-sizing: border-box;
      border-radius: 50%;
      margin: 20vh auto 10vh;
      overflow: hidden;
      img {
        width: 100%;
      }
    }
    h3 {
      font-size: 2rem;
      font-weight: 900;
      margin: 8px 0;
    }
  }
  
  .sectionSecond {
    background: $purp;
    height: 700px;
    padding: 60px 24px;
    border-top-left-radius: 40px;
    border-top-right-radius: 40px;
    color: #fff;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      background: $dpurp;
      height: 25%;
      width: 100px;
      bottom: 0;
      left: 0;
      opacity: 0;
    }
    .inner {
      position: relative;
      top: 60px;
      h3 {
        margin: 8px 0;
        opacity: 0;
        font-size: 2rem;
        font-weight: 900;
      }
      p {
        opacity: 0;
      }
      .btn-row {
        opacity: 0;
        a {
          background: $dpurp;
          width: 100%;
          border-radius: 18px;
          height: 60px;
          display: flex;
          justify-content: center;
          align-items: center;
          text-decoration: none;
          font-size: 1.1rem;
          color: #fff;
          text-transform: capitalize;
          font-weight: 600;
          margin-top: 48px;
        }
      }
    }
  }