.footer{
    height: 10vh;
    height: calc(var(--vh, 1vh) * 10);

    @include media('<=tablet'){
        display: none;
    }
    @include media('<=phone'){
        display: none;
    }
    .row{
        .social{
            .icons {
                width: 30vh;
                font-size: 3vh;
                display: flex;
                justify-content: space-evenly;
                margin-left: 5vh;
                .iconJS {
                    color: black;
                    transition: 1s;
                    display: flex;
                    width: 4vh;
                    height: 4vh;
                    justify-content: center;
                    align-items: center;
                    &:hover {
                      color: #61dafb;
                    -webkit-transform-origin: left/2 top/2;
                    -webkit-transform: rotate(-360deg) translateX(0%);
                    }
                  }
              }
        }
        
        .btn-row{
            z-index: 6;
            a {
                font-size: 3vh;
                color: $black;
                text-decoration: none;
                display: flex;
                align-items: center;
                font-weight: 600;
                @include media('<=tablet'){
                    font-size: 2vh;
                }
                @include media('<=phone'){
                    font-size: 1.5vh;
                }
                svg {
                    margin-left: 2vh;
                    border: 0.2vh solid $black;
                    padding: 2vh;
                    height: 2.2vh;
                    width: 2.2vh;
                    border-radius: 100px;
                    transition: .4s ease-in-out;
                    @include media('<=tablet'){
                        padding: 6px;
                        width: 20px;
                        height: 20px;
                    }
                    @include media('<=phone'){
                        padding: 6px;
                        width: 16px;
                        height: 16px;
                    }
                }
                &:hover {
                    svg {
                        background: $black;
                        color: $white;
                    }
                }
            }
        } 
    }   
}