.cases {
    height: 50vh;
    height: calc(var(--vh, 1vh) * 50);
    
    .cases-navigation {
      position: absolute;
      bottom: 0px;
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      z-index: 6;
      padding: 32px;
      box-sizing: border-box;
      @include media("<=tablet") {
        display: none;
      }
      .cases-arrow {
        background: rgba(0, 0, 0, 0.4);
        height: 72px;
        width: 72px;
        border-radius: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          color: $white;
          height: 28px;
          width: 28px;
        }
        &.disabled {
          opacity: 0.3;
        }
      }
    }
    .row {
      @include media("<=tablet") {
        flex-direction: column;
      }
      .case {
        position: relative;
        background: $black;
        cursor: pointer;
        
        &:hover {
          .case-image {
            opacity: 0.7;
          }
        }
        .case-details {
          width: 33.3333vw;
          height: 50vh;
          height: calc(var(--vh, 1vh) * 50);
          display: flex;
          flex-direction: column;
          justify-content: center;
          padding: 32px;
          box-sizing: border-box;
          z-index: 1;
          position: relative;
          @include media("<=tablet") {
            width: 100vw;
          }
          @include media("<=phone") {
            padding: 16px;
          }
          span {
            margin-top: 2vh;
            font-size: 2vh;
            opacity: 0.8;
            color: $white;
            font-weight: 600;
            @include media("<=desktop", ">tablet") {
              font-size: 2vh;
            }
            @include media("<=phone") {
              font-size: 2vh;
              line-height: 2.5vh;
            }
          }
          h2 {
            font-size: 3.4vh;
            line-height: 5vh;
            width: 85%;
            margin-top: 20vh;
            color: $white;
            @include media("<=desktop", ">tablet") {
              font-size: 3vh;
              line-height: 5vh;
            }
            @include media("<=phone") {
              font-size: 3.5vh;
              line-height: 5vh;
              margin-top: 30vh;
            }
          }
        }
        .pc {
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          position: absolute;
          opacity: 0.55;
          background-repeat: no-repeat;
          background-image: url("../../assets/pc.png");
          animation: CameraPan 20s infinite;
        }
        .books {
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          position: absolute;
          opacity: 0.55;
          background-repeat: no-repeat;
          background-image: url("../../assets/books.png");
          animation: CameraPanBooks 30s infinite;
        }
        .digital {
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          position: absolute;
          opacity: 0.55;
          background-repeat: no-repeat;
          background-image: url("../../assets/digital.png");
          animation: CameraPanDigital 15s infinite;
        }
        .case-image {
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          position: absolute;
          opacity: 0.55;
          background-repeat: no-repeat;
          background-image: url("../../assets/pc.png");
          animation: CameraPan 45s infinite;

          img{
            top: 0;
            left: 0;
            height: 100%;
            width: 100%;

        }
      }
    }
  }


  @keyframes CameraPan{
    0% {
      background-position: 0% 0%;
    }
    25% {
      background-position: 0% 40%;
    }
    50% {
      background-position: 40% 10%;
    }
    75% {
      background-position: 40% 0%;
    }
    100% {
      background-position: 0% 0%;
    }
  }

  @keyframes CameraPanBooks{
    0% {
      background-position: 0% 0%;
    }
    25% {
      background-position: 40% 40%;
    }
    50% {
      background-position: 20% 0%;
    }
    75% {
      background-position: 0% 60%;
    }
    100% {
      background-position: 0% 0%;
    }
  }

  @keyframes CameraPanDigital{
    0% {
      background-position: 0% 0%;
    }
    25% {
      background-position: 10% 40%;
    }
    50% {
      background-position: 10% 50%;
    }
    75% {
      background-position: 10% 40%;
    }
    100% {
      background-position: 0% 0%;
    }
  }

}